import React from 'react'
import { connect } from 'react-redux'
import { setSearchTerm, setCommandName, setCommandHistory } from '../../actions/SearchActions'
import { messages } from '../shared/Messages'

// Styles
import './Nav.scss'

const { string, func, array } = React.PropTypes

const Nav = React.createClass({
  propTypes: {
    searchTerm: string,
    commandHistory: array,
    setSearchTerm: func,
    setCommandName: func,
    setCommandHistory: func
  },
  handleClick (item) {
    console.log(item)
    this.props.setCommandName(item)
    var histObj = {
      'name': item,
      'msg': messages[item.toLowerCase()],
      'id': this.props.commandHistory.length + 1
    }
    this.props.setCommandHistory(histObj)
    this.props.setSearchTerm('')
  },
  render () {
    return (
      <nav className='global-nav'>
        <div className='nav-collection'>
          <div className='nav-item' onClick={() => this.handleClick('about')}>
            <a className='animated-line'>About</a>
          </div>
          <div className='nav-item' onClick={() => this.handleClick('legal experience')}>
            <a className='animated-line'>Legal Experience</a>
          </div>
          <div className='nav-item' onClick={() => this.handleClick('technology experience')}>
            <a className='animated-line'>Tech Experience</a>
          </div>
        </div>
        <div className='nav-collection'>
          <div className='nav-item' onClick={() => this.handleClick('services')}>
            <a className='animated-line'>Services</a>
          </div>
          <div className='nav-item' onClick={() => this.handleClick('contact')}>
            <a className='animated-line'>Contact</a>
          </div>
          <div className='nav-item'>
            <a href="/blog" className='animated-line'>Blog</a>
          </div>
        </div>
      </nav>
    )
  }
})

const mapDispatchToProps = (dispatch) => ({
  setSearchTerm (searchTerm) {
    dispatch(setSearchTerm(searchTerm))
  },
  setCommandName (commandName) {
    dispatch(setCommandName(commandName))
  },
  setCommandHistory (commandName) {
    dispatch(setCommandHistory(commandName))
  }
})

const mapStateToProps = (state) => ({
  searchTerm: state.searchTerm,
  commandHistory: state.commandHistory
})

export default connect(mapStateToProps, mapDispatchToProps)(Nav)



