import React from 'react'
import { render } from 'react-dom'
import { store } from './store'
import { Provider } from 'react-redux'
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider'
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import {grey500} from 'material-ui/styles/colors';

// Components
import HeaderComponent from './components/shared/Header'
import NavComponent from './components/shared/Nav'
// import AboutComponent from './components/home/About'
// import LegalComponent from './components/home/Legal'
// import SoftwareComponent from './components/home/Software'
import ContactComponent from './components/home/Contact'
import TerminalComponent from './components/terminal/Console'
import CommandLine from './components/terminal/CommandLine'

// Styles
import './ClientApp.scss'

const muiTheme = getMuiTheme({
  palette: {
    primary1Color: grey500
  }
})

const App = React.createClass({
  render () {
    return (
      <Provider store={store}>
        <MuiThemeProvider muiTheme={muiTheme}>
          <div className='wrapper'>
            <div className='main-content'>
              <div className='header-container'>
                <HeaderComponent />
                <NavComponent />
              </div>
              <div className='content-container'>
                <div className='hero-message'>
                  <h3>Your business is built on code and data. Nick is a lawyer that speaks your language.</h3>
                  <CommandLine />
                </div>
                <TerminalComponent />
              </div>
              <ContactComponent />
            </div>
          </div>
        </MuiThemeProvider>
      </Provider>
    )
  }
})

export function onLoad () {
  render(<App />, document.getElementById('app'))
}



