import React from 'react'

// Styles
import './LineItem.scss'

const { string, func } = React.PropTypes

const LineItem = React.createClass({
  propTypes: {
    commandName: string,
    commandMessage: string,
    updateWindowScroll: func
  },
  getInitialState () {
    return {
      'commandMessage': ''
    };
  },
  componentWillMount() {
    // console.log('next props', nextProps);
    var msg = (this.props.commandMessage) ? this.props.commandMessage : 'Command not found'
    console.log(msg)
    this.writeText(msg, 1, 5);
  },
  writeText(msg, index, interval) {
    var self = this
    if (index <= msg.length) {
      this.setState({
        commandMessage: msg.substring(0, index)
      })
      this.props.updateWindowScroll()
      setTimeout(function () { self.writeText(msg, index+1, interval); }, interval);
    }
  },
  render () {
    return (
      <div className='console-line-item'>
        <div className='prompt'>
          <span className='triangle'>▶</span>
          <span className='command'>~/nvanexan $</span>
          <pre className='old-command'>{this.props.commandName}</pre>
        </div>
        <div className='output'>
          <pre>{this.state.commandMessage}</pre>
        </div>
      </div>
    )
  }
})

export default LineItem



