import React from 'react'
import injectTapEventPlugin from 'react-tap-event-plugin'
injectTapEventPlugin();
import { connect } from 'react-redux'
import { setSearchTerm, setCommandName, setCommandHistory } from '../../actions/SearchActions'
import { messages } from '../shared/Messages'
import AutoComplete from 'material-ui/AutoComplete'

// Styles
import './CommandLine.scss'

const { string, func, array } = React.PropTypes

const CommandLine = React.createClass({
  propTypes: {
    searchTerm: string,
    commandHistory: array,
    setSearchTerm: func,
    setCommandName: func,
    setCommandHistory: func
  },
  getInitialState () {
    return {
      'dataSource': []
    };
  },
  componentWillMount() {
    var ds = []
    for (var prop in messages) {
      ds.push(prop)
    }
    this.setState({
      dataSource: ds.sort()
    })
  },
  handleUpdateInput (val) {
    this.props.setSearchTerm(val)
  },
  onFocus () {
    this.props.setSearchTerm('')
  },
  handleKeyPress () {
    this.props.setCommandName(this.props.searchTerm)
    var histObj = {
      'name': this.props.searchTerm,
      'msg': messages[this.props.searchTerm.toLowerCase().trim()],
      'id': this.props.commandHistory.length + 1
    }
    this.props.setCommandHistory(histObj)
  },
  render () {
    return (
      <div className='command-line'>
          <div>
            <AutoComplete
              hintText='Enter a command (e.g. "about")'
              dataSource={this.state.dataSource}
              fullWidth={true}
              searchText={this.props.searchTerm}
              onNewRequest={this.handleKeyPress}
              onUpdateInput={this.handleUpdateInput}
              style={{
                padding: '.35rem 1rem',
                border: 'solid 3px #eee',
                borderRadius: '3px',
                fontSize: '18px',
                fontWeight: '300'
              }}
              menuStyle={{
                maxHeight: '240px'
              }}
              filter={(searchText, key) => (key.indexOf(searchText) !== -1)}
              openOnFocus={true}
              onFocus={this.onFocus}
            />
          </div>
      </div>
    )
  }
})

const mapDispatchToProps = (dispatch) => ({
  setSearchTerm (searchTerm) {
    dispatch(setSearchTerm(searchTerm))
  },
  setCommandName (commandName) {
    dispatch(setCommandName(commandName))
  },
  setCommandHistory (commandName) {
    dispatch(setCommandHistory(commandName))
  }
})

const mapStateToProps = (state) => ({
  searchTerm: state.searchTerm,
  commandHistory: state.commandHistory
})

export default connect(mapStateToProps, mapDispatchToProps)(CommandLine)



