import React from 'react'
import { connect } from 'react-redux'
import LineItem from './LineItem'
import Fireworks from '../shared/Fireworks'

// Styles
import './Console.scss'

const { string, array } = React.PropTypes

const Console = React.createClass({
  propTypes: {
    searchTerm: string,
    commandName: string,
    commandHistory: array
  },
  updateWindowScroll() {
    this.refs.elem.scrollTop = this.refs.elem.scrollHeight
  },
  componentWillMount() {

  },
  render () {
    return (
      <div className='terminal__console'>
        <div className='terminal__window'>
          <div className='terminal__window-header'>
            <span className='icon icon-close'></span>
            <span className='icon icon-minimize'></span>
            <span className='icon icon-fullScreen'></span>
            <span className='title'>bash</span>
          </div>
          <div className='terminal__window-body' ref='elem'>
            <div className='terminal__window-body-console'>
              {
                this.props.commandHistory.map((h) => {
                  return (
                    <LineItem key={h.id} commandName={h.name} commandMessage={h.msg} updateWindowScroll={this.updateWindowScroll} />
                  )
                })
              }
              <div className='console-line-item'>
                <div className='prompt'>
                  <span className='triangle'>▶</span>
                  <span className='command'>~/nvanexan $</span>
                  <pre>{this.props.searchTerm}</pre>
                  <span className='caret'></span>
                </div>
              </div>
            </div>
          </div>
        </div>
        {
          this.props.commandName.toLowerCase() === 'fireworks' &&
          <Fireworks />
        }
      </div>
    )
  }
})

const mapStateToProps = (state) => ({
  searchTerm: state.searchTerm,
  commandName: state.commandName,
  commandHistory: state.commandHistory
})

export default connect(mapStateToProps)(Console)



