import React from 'react'

// Styles
import './Header.scss'

const Header = React.createClass({
  render () {
    return (
      <header className='header-component'>
        <h1 className='header-component__name'>Nicholas Van Exan</h1>
        <h2 className='header-component__title'>Privacy Lawyer &amp; Consultant</h2>
        <p className='header-component__intructions'>Type a command in the command window (e.g. 'about', 'legal experience', 'software') to learn more about Nick. Alternatively, you may use the buttons below.</p>
      </header>
    )
  }
})

export default Header



